import React from 'react';
import { Table } from '../common';
import { formatLimit } from '../../utils/format';
import { round } from '../../utils/round';
import { PricingItem } from '../../types';
import { PricingTable } from './styled';
import { CarrierFeesFootNotes } from '../carrier-fees/styled';

type TableWIthLimitsAndPricesProps = {
  titleHeader: string;
  firstColumnHeader: string;
  secondColumnHeader: string;
  firstList: PricingItem[];
  secondList: PricingItem[];
  messageType: 'SMS' | 'MMS';
};

const TableWithLimitsAndPrices = ({
  titleHeader,
  firstColumnHeader,
  secondColumnHeader,
  firstList,
  secondList,
  messageType,
}: TableWIthLimitsAndPricesProps) => {
  const limitSum = firstList.reduce((accumulator, item) => {
    return accumulator + (item.limit ?? 0);
  }, 0);
  const rowCount = firstList.length;

  return (
    <>
      <Table.Container>
        <Table.Wrapper>
          <PricingTable>
            <Table>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell>{titleHeader}</Table.HeaderCell>
                  <Table.HeaderCell>{firstColumnHeader}</Table.HeaderCell>
                  <Table.HeaderCell>{secondColumnHeader}</Table.HeaderCell>
                  <Table.HeaderCell>Unit</Table.HeaderCell>
                </Table.Row>
              </Table.Header>
              <Table.Body>
                {firstList.map((first, index) => {
                  const second = secondList[index];
                  const title =
                    index === 0
                      ? `First ${formatLimit(first.limit ?? 0)} messages`
                      : index === rowCount - 1
                      ? `Above ${formatLimit(limitSum)} messages`
                      : `Next ${formatLimit(first.limit ?? 0)} messages`;
                  const firstPrice =
                    first.price === 0
                      ? 'Free'
                      : `$${round(first.price, 5).toFixed(5)} `;
                  const secondPrice =
                    second.price === 0
                      ? 'Free'
                      : `$${round(second.price, 5).toFixed(5)}`;

                  return (
                    <Table.Row key={index}>
                      <Table.BodyCell>{title}</Table.BodyCell>
                      <Table.BodyCell>{firstPrice}</Table.BodyCell>
                      <Table.BodyCell>{secondPrice}</Table.BodyCell>
                      <Table.BodyCell>Per Segment</Table.BodyCell>
                    </Table.Row>
                  );
                })}
              </Table.Body>
            </Table>
          </PricingTable>
        </Table.Wrapper>
      </Table.Container>
      <CarrierFeesFootNotes>
        <p>¹Preferential and Standard Pricing.</p>
      </CarrierFeesFootNotes>
    </>
  );
};

export default TableWithLimitsAndPrices;
