import React, { FC, ReactFragment, useEffect, useRef, useState } from 'react';
import { RangeSliderContainer, RangeSliderHeader, RangeSliderValue } from './styled';
import { colors } from '../../../utils/colors';
import { Text } from '../Text';
import { formatLimit, formatNumberToCompactString } from '../../../utils/format';

type Props = {
  value: number;
  setValue: (value: number) => void;
  children?: React.ReactNode;
  minValue: number;
  maxValue: number;
  step: number;
};

export const RangeSlider: FC<Props> = ({ children, value, setValue, minValue, maxValue, step }) => {

  const sliderRef = useRef<HTMLInputElement>(null);

  const onSliderChange = (newValue: number) => {
    setValue(newValue);
    const valPercent = (Number(newValue) / Number(maxValue)) * 100;
    (
      sliderRef.current as HTMLInputElement
    ).style.background = `linear-gradient(to right, ${colors.secondary.gray700} ${valPercent}%, ${colors.secondary.gray200} ${valPercent}%)`;
  };

  useEffect(() => {
    onSliderChange(value);
  }, []);

  return (
    <RangeSliderContainer>
      <RangeSliderHeader>
        {children}
        <RangeSliderValue>
          <Text.Regular.Normal.md>{formatNumberToCompactString(Number(value))}</Text.Regular.Normal.md>
        </RangeSliderValue>
      </RangeSliderHeader>
      <input
        type="range"
        id="my-slider"
        min={String(minValue)}
        max={String(maxValue)}
        step={String(step)}
        value={value}
        onChange={(e) => onSliderChange(Number(e.currentTarget.value))}
        ref={sliderRef}
      ></input>
    </RangeSliderContainer>
  );
};
