import React, { useEffect, useState } from 'react';
import { calculatePrice } from '../../utils/calculatePrice';
import {
  CalculatorBlock,
  CalculatorCountingSection,
  CalculatorNumbersSection,
  CalculatorPriceSection,
  CalculatorSliderLabel,
  TotalPrice,
} from './styled';
import { Text } from '../common';
import { formatPrice } from '../../utils/format';
import data from '../../../data/Pricing.json';
import { PricingCategory } from '../../types';
import { RangeSlider } from '../common/RangeSlider';
import PhoneIcon from '../common/Icons/PhoneIcon/PhoneIcon';
import SMSIcon from '../common/Icons/SMSIcon/SMSIcon';
import MMSIcon from '../common/Icons/MMSIcon/MMSIcon';

const PriceCalculator = () => {
  const priceCalculatorData = JSON.parse(data.code) as unknown as PricingCategory[];

  const TELGORITHM_10DLC_MMS_SENT = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_MMS_SENT',
  )?.pricingItems;

  const TELGORITHM_10DLC_NUMBERS_PURCHASED = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_NUMBERS_PURCHASED',
  )?.pricingItems;

  const TELGORITHM_10DLC_SMS_SENT = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_SMS_SENT',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_MMS_SENT = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_MMS_SENT',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_NUMBERS_PURCHASED = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_NUMBERS_PURCHASED',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_SMS_SENT = priceCalculatorData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_SMS_SENT',
  )?.pricingItems;

  const [tenDlcNumbersAmount, setTenDlcNumbersAmount] = useState(1000);
  const [tenDlcSmsAmount, setTenDlcSmsAmount] = useState(2000000);
  const [tenDlcMmsAmount, setTenDlcMmsAmount] = useState(0);

  const [tollFreeNumbersAmount, setTollFreeNumbersAmount] = useState(1000);
  const [tollFreeSmsAmount, setTollFreeSmsAmount] = useState(800000);
  const [tollFreeMmsAmount, setTollFreeMmsAmount] = useState(0);

  const [tenDlcNumbersPrice, setTenDlcNumbersPrice] = useState<number>(0);
  const [tenDlcSmsPrice, setTenDlcSmsPrice] = useState<number>(0);
  const [tenDlcMmsPrice, setTenDlcMmsPrice] = useState<number>(0);
  const [tollFreeNumbersPrice, setTollFreeNumbersPrice] = useState<number>(0);
  const [tollFreeSmsPrice, setTollFreeSmsPrice] = useState<number>(0);
  const [tollFreeMmsPrice, setTollFreeMmsPrice] = useState<number>(0);

  const handle10DlcNumbersChange = (value: number) => {
    setTenDlcNumbersAmount(value);
    setTenDlcNumbersPrice(
      calculatePrice(TELGORITHM_10DLC_NUMBERS_PURCHASED ?? [], value ?? 0),
    );
  };
  const handle10DlcSmsChange = (value: number) => {
    setTenDlcSmsAmount(value);
    setTenDlcSmsPrice(calculatePrice(TELGORITHM_10DLC_SMS_SENT ?? [], value ?? 0));
  };
  const handle10DlcMmsChange = (value: number) => {
    setTenDlcMmsAmount(value);
    setTenDlcMmsPrice(calculatePrice(TELGORITHM_10DLC_MMS_SENT ?? [], value ?? 0));
  };
  const handleTollFreeNumbersChange = (value: number) => {
    setTollFreeNumbersAmount(value);
    setTollFreeNumbersPrice(
      calculatePrice(TELGORITHM_TOLLFREE_NUMBERS_PURCHASED ?? [], value ?? 0),
    );
  };
  const handleTollFreeSmsChange = (value: number) => {
    setTollFreeSmsAmount(value);
    setTollFreeSmsPrice(
      calculatePrice(TELGORITHM_TOLLFREE_SMS_SENT ?? [], value ?? 0),
    );
  };
  const handleTollFreeMmsChange = (value: number) => {
    setTollFreeMmsAmount(value);
    setTollFreeMmsPrice(
      calculatePrice(TELGORITHM_TOLLFREE_MMS_SENT ?? [], value ?? 0),
    );
  };

  const ourPrice =
    tenDlcNumbersPrice +
    tenDlcSmsPrice +
    tenDlcMmsPrice +
    tollFreeNumbersPrice +
    tollFreeSmsPrice +
    tollFreeMmsPrice;

  useEffect(() => {
    handle10DlcNumbersChange(tenDlcNumbersAmount);
    handle10DlcSmsChange(tenDlcSmsAmount);
    handleTollFreeNumbersChange(tollFreeNumbersAmount);
    handleTollFreeSmsChange(tollFreeSmsAmount);
  }, []);

  return (
    <CalculatorBlock>
      <CalculatorCountingSection>
        <CalculatorNumbersSection>
          <RangeSlider
            value={tenDlcNumbersAmount}
            setValue={handle10DlcNumbersChange}
            minValue={100}
            maxValue={10000}
            step={100}
          >
            <CalculatorSliderLabel color='#4245d1'>
              <PhoneIcon color='#4245d1'/>
              <Text.Regular.Normal.md>10DLC Numbers</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
          <RangeSlider
            value={tenDlcSmsAmount}
            setValue={handle10DlcSmsChange}
            minValue={500000}
            maxValue={10000000}
            step={100000}
          >
            <CalculatorSliderLabel>
              <SMSIcon />
              <Text.Regular.Normal.md>Outbound SMS</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
          <RangeSlider
            value={tenDlcMmsAmount}
            setValue={handle10DlcMmsChange}
            minValue={500000}
            maxValue={10000000}
            step={100000}
          >
            <CalculatorSliderLabel>
              <MMSIcon />
              <Text.Regular.Normal.md>Outbound MMS</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
        </CalculatorNumbersSection>
        <CalculatorNumbersSection>
          <RangeSlider
            value={tollFreeNumbersAmount}
            setValue={handleTollFreeNumbersChange}
            minValue={100}
            maxValue={10000}
            step={100}
          >
            <CalculatorSliderLabel color="#e85747">
              <PhoneIcon color="#e85747"/>
              <Text.Regular.Normal.md>Toll-Free Numbers</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
          <RangeSlider
            value={tollFreeSmsAmount}
            setValue={handleTollFreeSmsChange}
            minValue={500000}
            maxValue={10000000}
            step={100000}
          >
            <CalculatorSliderLabel>
              <SMSIcon />
              <Text.Regular.Normal.md>Outbound SMS</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
          <RangeSlider
            value={tollFreeMmsAmount}
            setValue={handleTollFreeMmsChange}
            minValue={500000}
            maxValue={10000000}
            step={100000}
          >
            <CalculatorSliderLabel>
              <MMSIcon />
              <Text.Regular.Normal.md>Outbound MMS</Text.Regular.Normal.md>
            </CalculatorSliderLabel>
          </RangeSlider>
        </CalculatorNumbersSection>
      </CalculatorCountingSection>
      <CalculatorPriceSection>
        <TotalPrice>
          <Text.Regular.Normal.md>Your monthly price</Text.Regular.Normal.md>
          <Text.Title.Bold.md>${formatPrice(ourPrice, ourPrice)}</Text.Title.Bold.md>
        </TotalPrice>
      </CalculatorPriceSection>
    </CalculatorBlock>
  );
};

export default PriceCalculator;
