export const PricingSectionsDropdown = styled.div`
  margin-top: 30px;
  @media ${device.laptop} {
    display: none;
  }
`;

export enum PricingTabsName {
  MonthlyRecurringFees = 'Monthly Recurring Fees',
  CallForwardingFee = 'Call Forwarding Fee',
  NumberLookUpFee = 'Number Look-up Fee',
  TenDLCNumberSMSPricing = '10DLC Number SMS Pricing',
  TenDLCNumberMMSPricing = '10DLC Number MMS Pricing',
  TollFreeNumberSMSPricing = 'Toll-Free Number SMS Pricing',
  TollFreeNumberMMSPricing = 'Toll-Free Number MMS Pricing',
}

const RenderPricingTable = ({
  pricingCategory,
}: {
  pricingCategory: PricingTabsName;
}) => {
  const pricingData = JSON.parse(data.code) as unknown as PricingCategory[];

  const TELGORITHM_10DLC_MMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_MMS_SENT',
  )?.pricingItems;

  const TELGORITHM_10DLC_MMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_MMS_RECEIVED',
  )?.pricingItems;

  const TELGORITHM_10DLC_SMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_SMS_SENT',
  )?.pricingItems;

  const TELGORITHM_10DLC_SMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_10DLC_SMS_RECEIVED',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_MMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_MMS_SENT',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_MMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_MMS_RECEIVED',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_SMS_SENT = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_SMS_SENT',
  )?.pricingItems;

  const TELGORITHM_TOLLFREE_SMS_RECEIVED = pricingData.find(
    (category) => category.categoryName === 'TELGORITHM_TOLLFREE_SMS_RECEIVED',
  )?.pricingItems;

  switch (pricingCategory) {
    case PricingTabsName.MonthlyRecurringFees:
      return <MonthlyRecurringTable />;
    case PricingTabsName.CallForwardingFee:
      return <CallForwardingTable />;
    case PricingTabsName.NumberLookUpFee:
      return <NumberLookUpTable />;
    case PricingTabsName.TenDLCNumberSMSPricing:
      return (
        <TableWithLimitsAndPrices
          titleHeader="Monthly Text Volume"
          firstColumnHeader="Per Message Fee (Received)¹"
          secondColumnHeader="Per Message Fee (Sent)"
          firstList={TELGORITHM_10DLC_SMS_RECEIVED ?? []}
          secondList={TELGORITHM_10DLC_SMS_SENT ?? []}
          messageType="SMS"
        />
      );
    case PricingTabsName.TenDLCNumberMMSPricing:
      return (
        <TableWithLimitsAndPrices
          titleHeader="Monthly Text Volume"
          firstColumnHeader="Per Message Fee (Received)¹"
          secondColumnHeader="Per Message Fee (Sent)"
          firstList={TELGORITHM_10DLC_MMS_RECEIVED ?? []}
          secondList={TELGORITHM_10DLC_MMS_SENT ?? []}
          messageType="MMS"
        />
      );
    case PricingTabsName.TollFreeNumberSMSPricing:
      return (
        <TableWithLimitsAndPrices
          titleHeader="Monthly Text Volume"
          firstColumnHeader="Per Message Fee (Received)¹"
          secondColumnHeader="Per Message Fee (Sent)"
          firstList={TELGORITHM_TOLLFREE_SMS_RECEIVED ?? []}
          secondList={TELGORITHM_TOLLFREE_SMS_SENT ?? []}
          messageType="SMS"
        />
      );
    case PricingTabsName.TollFreeNumberMMSPricing:
      return (
        <TableWithLimitsAndPrices
          titleHeader="Monthly Text Volume"
          firstColumnHeader="Per Message Fee (Received)¹"
          secondColumnHeader="Per Message Fee (Sent)"
          firstList={TELGORITHM_TOLLFREE_MMS_RECEIVED ?? []}
          secondList={TELGORITHM_TOLLFREE_MMS_SENT ?? []}
          messageType="MMS"
        />
      );
    default:
      return <></>;
  }
};

const FeesSection = () => {
  const [activeTab, setActiveTab] = useState<PricingTabsName>(
    PricingTabsName.TenDLCNumberSMSPricing,
  );

  const activeTabNumber = Object.values(PricingTabsName).findIndex(
    (item) => item === activeTab,
  );

  console.log(activeTabNumber);
  return (
    <StyledFeesSection>
      <PricingSectionsDropdown>
        <DropdownInput
          value={activeTab}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            setActiveTab(event.currentTarget.value as PricingTabsName)
          }
        >
          <option value={PricingTabsName.MonthlyRecurringFees}>
            {PricingTabsName.MonthlyRecurringFees}
          </option>
          <option value={PricingTabsName.CallForwardingFee}>
            {PricingTabsName.CallForwardingFee}
          </option>
          <option value={PricingTabsName.NumberLookUpFee}>
            {PricingTabsName.NumberLookUpFee}
          </option>
          <option value={PricingTabsName.TenDLCNumberSMSPricing}>
            {PricingTabsName.TenDLCNumberSMSPricing}
          </option>
          <option value={PricingTabsName.TenDLCNumberMMSPricing}>
            {PricingTabsName.TenDLCNumberMMSPricing}
          </option>
          <option value={PricingTabsName.TollFreeNumberSMSPricing}>
            {PricingTabsName.TollFreeNumberSMSPricing}
          </option>
          <option value={PricingTabsName.TollFreeNumberMMSPricing}>
            {PricingTabsName.TollFreeNumberMMSPricing}
          </option>
        </DropdownInput>
      </PricingSectionsDropdown>
      <PricingTabs activeTabNumber={activeTabNumber}>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.MonthlyRecurringFees)}
          isActive={activeTab === PricingTabsName.MonthlyRecurringFees}
        >
          <TimerWithArrow />
          <Text.Regular.Normal.md>Monthly Recurring Fees</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.CallForwardingFee)}
          isActive={activeTab === PricingTabsName.CallForwardingFee}
        >
          <RightSmallArrow />
          <Text.Regular.Normal.md>Call Forwarding Fee</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.NumberLookUpFee)}
          isActive={activeTab === PricingTabsName.NumberLookUpFee}
        >
          <Magnifier />
          <Text.Regular.Normal.md>Number Look-up Fee</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.TenDLCNumberSMSPricing)}
          isActive={activeTab === PricingTabsName.TenDLCNumberSMSPricing}
        >
          <SMSIcon />
          <Text.Regular.Normal.md>10DLC Number SMS Pricing</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.TenDLCNumberMMSPricing)}
          isActive={activeTab === PricingTabsName.TenDLCNumberMMSPricing}
        >
          <MMSIcon />
          <Text.Regular.Normal.md>10DLC Number MMS Pricing</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.TollFreeNumberSMSPricing)}
          isActive={activeTab === PricingTabsName.TollFreeNumberSMSPricing}
        >
          <SMSIcon />
          <Text.Regular.Normal.md>Toll-Free Number SMS Pricing</Text.Regular.Normal.md>
        </PricingTab>
        <PricingTab
          onClick={() => setActiveTab(PricingTabsName.TollFreeNumberMMSPricing)}
          isActive={activeTab === PricingTabsName.TollFreeNumberMMSPricing}
        >
          <MMSIcon />
          <Text.Regular.Normal.md> Toll-Free Number MMS Pricing</Text.Regular.Normal.md>
        </PricingTab>
      </PricingTabs>
      <PricingTableContainer>
        <RenderPricingTable pricingCategory={activeTab} />
      </PricingTableContainer>
    </StyledFeesSection>
  );
};

export default FeesSection;
